<template>
  <div>
    <div class="nv-row mb-4">
      <div class="nv-col w-full">
        <label class="vs-input--label">{{ $t('ACTIVITY_DESCRIPTION') }}</label>
        <vs-input class="w-full" v-model="mutableUserData.activityDescription"/>
      </div>
    </div>

    <div class="nv-row mb-4">
      <div class="nv-col w-full">
        <label class="vs-input--label">{{ $t('TASK_TYPE') }}</label>
        <ul class="flex justify-start mt-2">
          <li class="mr-4" v-for="(enumeration, index) in taskTypes" :key="index">
            <vs-radio v-model="mutableUserData.taskTypeEnumId" :vs-value="enumeration.enumId">{{ enumeration.description }}</vs-radio>
          </li>
        </ul>
      </div>
    </div>

    <div class="nv-row mb-4">
      <div class="nv-col w-full">
        <div class="flex justify-left mb-2">
          <div class="vs-input--label mr-2">{{ $t('CROWDS') }}</div>
          <div>
            <vs-button class="m-0 p-0" color="primary" type="flat" @click="showCrowdForm">
              <i class="far fa-plus-circle text-primary"></i>
            </vs-button>
          </div>
        </div>

        <div class="inline-form p-4 mt-4 mb-4" v-if="crowdFormVisible">
          <div class="nv-row mb-4">
            <div class="nv-col sm:w-1/3 w-full">
              <vs-select autocomplete class="w-full" v-model="crowdTypeEnumId">
                <vs-select-item v-for="(enumeration, index) in crowdTypes" :key="index" :value="enumeration.enumId" :text="enumeration.description"/>
              </vs-select>
            </div>
            <div class="nv-col sm:w-2/3 w-full pl-0">
              <user-lookup :title="$t('USER')" valueField="userId" displayField="userFullName" @select-record="selectUserRecord" v-show="crowdTypeEnumId === 'WF_CROWD_USER'"/>
              <user-group-lookup :title="$t('USER_GROUP')" valueField="userGroupId" displayField="description" @select-record="selectUserGroupRecord" v-show="crowdTypeEnumId === 'WF_CROWD_USER_GROUP'"/>
              <vs-input class="w-full" :disabled="true" v-show="crowdTypeEnumId === 'WF_CROWD_INITIATOR'"/>
            </div>
          </div>

          <div class="nv-row mb-4" v-if="mutableUserData.taskTypeEnumId === 'WF_TASK_APPROVAL' && crowdTypeEnumId === 'WF_CROWD_USER_GROUP'">
            <div class="nv-col sm:w-1/2 w-full required">
              <label class="vs-input--label">{{ $t('MIN_APPROVALS') }}</label>
              <vs-input class="w-full" v-model="minApprovals" type="number"/>
            </div>
            <div class="nv-col sm:w-1/2 w-full required">
              <label class="vs-input--label">{{ $t('MIN_REJECTIONS') }}</label>
              <vs-input class="w-full" v-model="minRejections" type="number"/>
            </div>
          </div>

          <div class="nv-row">
            <div class="nv-col w-full">
              <vs-button class="px-5 py-3 mr-4 border-solid" @click="createCrowd" color="primary" type="filled">{{ $t('SUBMIT') }}</vs-button>
              <vs-button class="p-3 mr-4" @click="hideCrowdForm" color="primary" type="border">{{ $t('CANCEL') }}</vs-button>
            </div>
          </div>
        </div>

        <div class="alert-container" v-if="mutableUserData.crowds.length === 0">
          <vs-alert :active.sync="mutableUserData.crowds.length === 0" color="warning">
            <i class="far fa-exclamation-circle pr-1"></i> {{ $t('NO_DATA_FOUND') }}
          </vs-alert>
        </div>
        <vs-table :data="mutableUserData.crowds" class="mb-4 table-no-header" v-else>
          <template slot="thead">
            <vs-th v-if="mutableUserData.taskTypeEnumId === 'WF_TASK_APPROVAL'" sort-key="logicalGroup">
              {{ $t('GROUP') }}
            </vs-th>
            <vs-th>
              {{ $t('CROWD') }}
            </vs-th>
            <vs-th v-if="mutableUserData.taskTypeEnumId === 'WF_TASK_APPROVAL'">
              {{ $t('MIN_APPROVALS') }}
            </vs-th>
            <vs-th>
              {{ $t('OPTIONS') }}
            </vs-th>
          </template>
          <template  slot-scope="{data}">
            <vs-tr :key="index" v-for="(tr, index) in data" :data="tr">
              <vs-td v-if="mutableUserData.taskTypeEnumId === 'WF_TASK_APPROVAL'" class="text-center">
                <vs-dropdown vs-trigger-click>
                  <vs-button class="btn-drop" :color="groupColor(data[index])" type="border" icon-pack="fas" icon="fa-circle"></vs-button>
                  <vs-dropdown-menu class="dropdown-small">
                    <vs-dropdown-item v-for="(color, colorIndex) in groupColors" :key="colorIndex" @click="data[index].logicalGroup = colorIndex">
                      <vs-icon :color="groupColors[colorIndex]" icon-pack="fas" icon="fa-circle"></vs-icon>
                    </vs-dropdown-item>
                  </vs-dropdown-menu>
                </vs-dropdown>
              </vs-td>
              <vs-td :data="data[index]">
                {{ crowdName(data[index]) }}
              </vs-td>
              <vs-td :data="data[index]" v-if="mutableUserData.taskTypeEnumId === 'WF_TASK_APPROVAL'">
                <span class="mr-2"><i class="far fa-check-circle text-success"></i> {{ data[index].minApprovals }}</span>
                <span class="mr-4"><i class="far fa-times-circle text-danger"></i> {{ data[index].minRejections }}</span>
              </vs-td>
              <vs-td :data="data[index]">
                <vs-button class="p-3 mr-4" color="danger" @click="deleteCrowd(index)" icon-pack="far" icon="fa-trash-alt">{{ $t('DELETE') }}</vs-button>
              </vs-td>
            </vs-tr>
          </template>
        </vs-table>
      </div>
    </div>

    <div class="nv-row mb-4" v-if="mutableUserData.taskTypeEnumId === 'WF_TASK_MANUAL'">
      <div class="nv-col w-full required">
        <label class="vs-input--label">{{ $t('SUMMARY') }}</label>
        <vs-input class="w-full" v-model="mutableUserData.summary"/>
      </div>
    </div>

    <div class="nv-row mb-4" v-if="mutableUserData.taskTypeEnumId === 'WF_TASK_MANUAL'">
      <div class="nv-col w-full">
        <label class="vs-input--label">{{ $t('DESCRIPTION') }}</label>
        <vs-textarea v-model="mutableUserData.description"/>
      </div>
    </div>

    <div class="nv-row mb-4" v-if="mutableUserData.taskTypeEnumId === 'WF_TASK_VARIABLE'">
      <div class="nv-col w-full required">
        <label class="vs-input--label">{{ $t('VARIABLE') }}</label>
        <vs-select autocomplete class="w-full" v-model="mutableUserData.variableId">
          <vs-select-item v-for="(variable, index) in variables" :key="index" :value="variable.variableId" :text="variable.variableName"/>
        </vs-select>
      </div>
    </div>

    <div class="nv-row mb-4">
      <div class="nv-col w-full">
        <label class="vs-input--label">{{ $t('TIMEOUT') }}</label>
        <div class="flex">
          <div class="flex mr-4">
            <vs-select autocomplete v-model="mutableUserData.timeoutUomId">
              <vs-select-item v-for="(interval, index) in timeIntervals" :key="index" :value="interval.uomId" :text="interval.description"/>
            </vs-select>
          </div>
          <div class="flex">
            <vs-input-number v-model="mutableUserData.timeoutInterval" min="0"/>
          </div>
        </div>
      </div>
    </div>

    <div class="nv-row mb-4">
      <div class="nv-col w-full">
        <vs-button class="px-5 py-3 mr-4 border-solid" @click="save" color="primary" type="filled">{{ $t('SAVE') }}</vs-button>
        <vs-button class="p-3 mr-4" @click="cancel" color="primary" type="border" t>{{ $t('CANCEL') }}</vs-button>
      </div>
    </div>
  </div>
</template>

<script>
import UserLookup from '@/views/nucleus/user/UserLookup'
import UserGroupLookup from '@/views/nucleus/user-group/UserGroupLookup'
import WorkflowActivityMixin from '@/mixins/commons/WorkflowActivityMixin'
import WorkflowCrowdActivityMixin from '@/mixins/commons/WorkflowCrowdActivityMixin'
import WorkflowGroupActivityMixin from '@/mixins/commons/WorkflowGroupActivityMixin'
import WorkflowTimeoutActivityMixin from '@/mixins/commons/WorkflowTimeoutActivityMixin'
import EnumerationService from '@/services/commons/EnumerationService'
import WorkflowService from '@/services/commons/WorkflowService'

export default {
  name: 'user-activity-props',
  mixins: [WorkflowActivityMixin, WorkflowCrowdActivityMixin, WorkflowGroupActivityMixin, WorkflowTimeoutActivityMixin],
  components: {
    UserLookup,
    UserGroupLookup
  },
  data: function () {
    return {
      taskTypes: [],
      variables: [],
      minApprovals: 1,
      minRejections: 1
    }
  },
  methods: {
    async load () {
      let self = this
      EnumerationService.enumerations({
        enumTypeId: 'WorkflowTaskType',
        orderByField: 'sequenceNum,description'
      }).then(function (response) {
        self.taskTypes = response.data.enumerationList || []
      })

      EnumerationService.enumerations({
        enumTypeId: 'WorkflowCrowdType',
        orderByField: 'sequenceNum,description'
      }).then(function (response) {
        self.crowdTypes = response.data.enumerationList || []
      })

      WorkflowService.workflowVariables({
        workflowId: this.workflow.workflowId,
        orderByField: 'variableName'
      }).then(function (response) {
        self.variables = response.data.workflowVariableList || []
      })
    },
    createCrowd () {
      if (this.crowdTypeEnumId === 'WF_CROWD_INITIATOR') {
        this.mutableUserData.crowds.push({
          logicalGroup: this.logicalGroup,
          crowdTypeEnumId: this.crowdTypeEnumId,
          minApprovals: 1,
          minRejections: 1
        })
      } else if (this.crowdTypeEnumId === 'WF_CROWD_USER' && this.userId) {
        this.mutableUserData.crowds.push({
          logicalGroup: this.logicalGroup,
          crowdTypeEnumId: this.crowdTypeEnumId,
          userId: this.userId,
          userFullName: this.userFullName,
          minApprovals: 1,
          minRejections: 1
        })
      } else if (this.crowdTypeEnumId === 'WF_CROWD_USER_GROUP' && this.userGroupId) {
        this.mutableUserData.crowds.push({
          logicalGroup: this.logicalGroup,
          crowdTypeEnumId: this.crowdTypeEnumId,
          userGroupId: this.userGroupId,
          userGroupDescription: this.userGroupDescription,
          minApprovals: this.minApprovals,
          minRejections: this.minRejections
        })
      }
      this.hideCrowdForm()
    },
    deleteCrowd (index) {
      this.mutableUserData.crowds.splice(index, 1)
    }
  },
  mounted () {
    this.load()
  }
}
</script>
