<template>
  <div>
    <div class="nv-row mb-4">
      <div class="nv-col w-full">
        <label class="vs-input--label">{{ $t('ACTIVITY_DESCRIPTION') }}</label>
        <vs-input class="w-full" v-model="mutableUserData.activityDescription"/>
      </div>
    </div>

    <div class="nv-row mb-4">
      <div class="nv-col w-full">
        <label class="vs-input--label">{{ $t('ADJUSTMENT_TYPE') }}</label>
        <ul class="flex justify-start mt-2">
          <li class="mr-4" v-for="(enumeration, index) in adjustmentTypes" :key="index">
            <vs-radio v-model="mutableUserData.adjustmentTypeEnumId" :vs-value="enumeration.enumId">{{ enumeration.description }}</vs-radio>
          </li>
        </ul>
      </div>
    </div>

    <div class="nv-row mb-4" v-if="mutableUserData.adjustmentTypeEnumId === 'WF_ADJUST_STATUS'">
      <div class="nv-col w-full required">
        <label class="vs-input--label">{{ $t('STATUS') }}</label>
        <vs-select autocomplete class="w-full" :placeholder="$t('SELECT_VALUE')" v-model="mutableUserData.statusId" @change="statusValueChanged">
          <vs-select-item v-for="(statusItem, index) in statusItems" :key="index" :value="statusItem.statusId" :text="statusItem.description"/>
        </vs-select>
      </div>
    </div>

    <div class="nv-row mb-4" v-if="mutableUserData.adjustmentTypeEnumId === 'WF_ADJUST_VARIABLE'">
      <div class="nv-col w-full required">
        <label class="vs-input--label">{{ $t('VARIABLE') }}</label>
        <vs-select autocomplete class="w-full" :placeholder="$t('SELECT_VALUE')" v-model="mutableUserData.variableId" @change="variableValueChanged">
          <vs-select-item v-for="(variable, index) in variables" :key="index" :value="variable.variableId" :text="variable.variableName"/>
        </vs-select>
      </div>
    </div>

    <div class="nv-row mb-4" v-if="mutableUserData.adjustmentTypeEnumId === 'WF_ADJUST_VARIABLE'">
      <div class="nv-col w-full required">
        <label class="vs-input--label">{{ $t('SCRIPT') }}</label>
        <div class="code-editor">
          <codemirror v-model="mutableUserData.definedValue" :options="javascriptOptions" class="workflow-code-editor"></codemirror>
        </div>
      </div>
    </div>

    <div class="nv-row mb-4">
      <div class="nv-col w-full">
        <vs-button class="px-5 py-3 mr-4 border-solid" @click="save" color="primary" type="filled">{{ $t('SAVE') }}</vs-button>
        <vs-button class="p-3 mr-4" @click="cancel" color="primary" type="border" t>{{ $t('CANCEL') }}</vs-button>
      </div>
    </div>
  </div>
</template>

<script>
import CodeEditorMixin from '@/mixins/commons/CodeEditorMixin'
import WorkflowActivityMixin from '@/mixins/commons/WorkflowActivityMixin'
import EnumerationService from '@/services/commons/EnumerationService'
import StatusFlowService from '@/services/commons/StatusFlowService'
import WorkflowService from '@/services/commons/WorkflowService'

export default {
  name: 'adjustment-activity-props',
  mixins: [CodeEditorMixin, WorkflowActivityMixin],
  data: function () {
    return {
      adjustmentTypes: [],
      statusItems: [],
      variables: []
    }
  },
  methods: {
    async load () {
      let self = this
      EnumerationService.enumerations({
        enumTypeId: 'WorkflowAdjustmentType',
        orderByField: 'sequenceNum,description'
      }).then(function (response) {
        self.adjustmentTypes = response.data.enumerationList || []
      })

      WorkflowService.workflowVariables({
        workflowId: this.workflow.workflowId,
        orderByField: 'variableName'
      }).then(function (response) {
        self.variables = response.data.workflowVariableList || []
      })

      StatusFlowService.statusFlowItems({
        statusFlowId: this.workflow.statusFlowId,
        orderByField: 'sequenceNum,description'
      }).then(function (response) {
        self.statusItems = response.data.statusItemList || []
      })
    },
    statusValueChanged (value) {
      for (let i = 0; i < this.statusItems.length; i++) {
        if (this.statusItems[i].statusId === value) {
          this.mutableUserData.statusDescription = this.statusItems[i].description
        }
      }
    },
    variableValueChanged (value) {
      for (let i = 0; i < this.variables.length; i++) {
        if (this.variables[i].variableId === value) {
          this.mutableUserData.variableName = this.variables[i].variableName
        }
      }
    }
  },
  mounted () {
    this.load()
  }
}
</script>
