<template id="popup">
  <vs-popup fullscreen :title="title" :active.sync="activeInternal">
    <div :ref="popupRef" class="vs-con-loading__container">
      <vs-table class="mb-base" :data="workflowTableData" :noDataText="workflowTableNoDataText" :sst="true" search @search="filterWorkflowTable" @sort="orderWorkflowTable" @selected="selectRow">
        <div slot="header" class="flex flex-wrap-reverse items-center flex-grow justify-between">
          <div class="flex flex-wrap-reverse items-center">
            <vs-button class="p-3 mr-4" @click="loadWorkflowTableData(popupRef)" type="filled" icon="fa-sync" icon-pack="far" :disabled="workflowTableBusy">{{ $t('REFRESH') }}</vs-button>
          </div>

          <div class="flex justify-end mr-4">
            <div class="p-2 border border-solid border-grey-light rounded-full d-theme-dark-bg flex items-center justify-between font-medium">
              <span class="mx-2">{{ workflowTablePagingInfo }}</span>
              <vs-dropdown vs-trigger-click class="cursor-pointer mr-2">
                <span class="far fa-angle-down text-primary"></span>
                <vs-dropdown-menu>
                  <vs-dropdown-item :key="index" v-for="(item, index) in tablePageOptions" @click="workflowTablePerPage = item">
                    <span>{{ item }}</span>
                  </vs-dropdown-item>
                </vs-dropdown-menu>
              </vs-dropdown>
            </div>
          </div>
        </div>

        <template slot="thead">
          <vs-th sort-key="workflowId">
            {{ this.$t('ID') }}
          </vs-th>
          <vs-th sort-key="workflowName">
            {{ this.$t('WORKFLOW_NAME') }}
          </vs-th>
          <vs-th sort-key="workflowTypeName">
            {{ this.$t('WORKFLOW_TYPE') }}
          </vs-th>
        </template>

        <template slot-scope="{data}">
          <vs-tr :key="index" v-for="(tr, index) in data" :data="tr">
            <vs-td :data="data[index].workflowId">
              {{ data[index].workflowId }}
            </vs-td>
            <vs-td :data="data[index].workflowName">
              <span :class="disabledClass(data[index])">{{ data[index].workflowName }}</span>
            </vs-td>
            <vs-td :data="data[index].workflowTypeName">
              {{ data[index].workflowTypeName }}
            </vs-td>
          </vs-tr>
        </template>
      </vs-table>

      <vs-pagination :total="workflowTableTotalPages" v-model="workflowTableCurrentPage" :max="maxPaginationItems"></vs-pagination>
    </div>
  </vs-popup>
</template>

<script>
import DisabledClassMixin from '@/mixins/commons/DisabledClassMixin'
import PopupMixin from '@/mixins/commons/PopupMixin'
import WorkflowTableMixin from '@/mixins/commons/WorkflowTableMixin'

export default {
  name: 'workflow-popup',
  mixins: [DisabledClassMixin, PopupMixin, WorkflowTableMixin],
  mounted: function () {
    this.loadWorkflowTableData(this.popupRef)
  },
  beforeDestroy: function () {
    this.$el.remove()
  }
}
</script>
