let WorkflowGroupActivityMixin = {
  data () {
    return {
      logicalGroup: 0,
      groupColors: [
        'royalblue',
        'seagreen',
        'crimson',
        'orange',
        'gold',
        'darkviolet',
        'gray',
        'black'
      ]
    }
  },
  computed: {
    currentGroupColor: function () {
      return this.groupColors[this.logicalGroup]
    }
  },
  methods: {
    groupColor (item) {
      return this.groupColors[item.logicalGroup]
    }
  }
}

export default WorkflowGroupActivityMixin
