<template>
  <div>
    <div class="nv-row mb-4">
      <div class="nv-col w-full">
        <label class="vs-input--label">{{ $t('ACTIVITY_DESCRIPTION') }}</label>
        <vs-input class="w-full" v-model="mutableUserData.activityDescription"/>
      </div>
    </div>

    <div class="nv-row mb-4">
      <div class="nv-col w-full required">
        <label class="vs-input--label">{{ $t('SERVICE') }}</label>
        <vs-select autocomplete class="w-full" :placeholder="$t('SELECT_VALUE')" v-model="mutableUserData.serviceName" @change="serviceValueChanged">
          <vs-select-item v-for="(service, index) in services" :key="index" :value="service.serviceName" :text="service.displayName"/>
        </vs-select>
      </div>
    </div>

    <div class="nv-row mb-4" v-if="mutableUserData.acceptsParameters">
      <div class="nv-col w-full">
        <label class="vs-input--label">{{ $t('PARAMETERS') }}</label>
        <div class="code-editor">
          <codemirror v-model="mutableUserData.parameters" :options="jsonOptions" class="workflow-code-editor"></codemirror>
        </div>
      </div>
    </div>

    <div class="nv-row mb-4">
      <div class="nv-col w-full">
        <vs-button class="px-5 py-3 mr-4 border-solid" @click="save" color="primary" type="filled">{{ $t('SAVE') }}</vs-button>
        <vs-button class="p-3 mr-4" @click="cancel" color="primary" type="border" t>{{ $t('CANCEL') }}</vs-button>
      </div>
    </div>
  </div>
</template>

<script>
import CodeEditorMixin from '@/mixins/commons/CodeEditorMixin'
import WorkflowActivityMixin from '@/mixins/commons/WorkflowActivityMixin'
import WorkflowService from '@/services/commons/WorkflowService'

export default {
  name: 'service-activity-props',
  mixins: [CodeEditorMixin, WorkflowActivityMixin],
  data: function () {
    return {
      services: []
    }
  },
  methods: {
    async load () {
      let self = this
      WorkflowService.workflowTypeServices({
        workflowTypeId: this.workflow.workflowTypeId,
        orderByField: 'displayName'
      }).then(function (response) {
        self.services = response.data.workflowTypeServiceList || []
      })
    },
    serviceValueChanged (value) {
      for (let i = 0; i < this.services.length; i++) {
        if (this.services[i].serviceName === value) {
          this.mutableUserData.serviceName = this.services[i].serviceName
          this.mutableUserData.acceptsParameters = this.services[i].acceptsParameters === 'Y'
        }
      }
    }
  },
  mounted () {
    this.load()
  }
}
</script>
