<template>
  <div v-on:keyup.esc="hidePropsForm" tabindex="0" ref="main">
    <div class="alert-container" v-if="alertActive">
      <vs-alert :active.sync="alertActive" :color="alertVariant" closable icon-pack="feather" close-icon="icon-x" class="mb-5">
        <p>{{ alertMessage }}</p>
      </vs-alert>
    </div>

    <div class="alert-container" v-if="draftAlertActive">
      <vs-alert :active.sync="draftAlertActive" color="warning" closable icon-pack="feather" close-icon="icon-x" class="mb-5">
        <p>{{ $t('LOAD_DRAFT_DESIGN') }}
          <vs-button class="p-0 pb-1 mx-2" color="dark" type="line" line-origin="left" @click="loadDraft">{{ $t('YES') }}</vs-button>
          <vs-button class="p-0 pb-1 mx-2" color="dark" type="line" line-origin="left" @click="draftAlertActive = false">{{ $t('NO') }}</vs-button>
        </p>
      </vs-alert>
    </div>

    <workflow-popup :title="$t('DUPLICATE')"
                    :active="popupActive"
                    :ignoreWorkflowId="workflow.workflowId"
                    :workflowTypeId="workflow.workflowTypeId"
                    @select-row="duplicateWorkflow"
                    v-on:close="popupActive = false"/>

    <nv-card id="loading-container" class="vs-con-loading__container">
      <div class="flex justify-between toolbar mb-4" v-if="$acl.check('isNucleusAll')">
        <div class="flex">
          <vs-button class="mr-2 px-3" color="primary" type="border" @click="cancel">{{ $t('CANCEL') }}</vs-button>
          <nv-input-group class="mr-2">
            <template slot="prepend">
              <nv-tooltip :text="$t('NEW')">
                <div class="prepend-text btn-addon">
                  <vs-button color="danger" type="filled" @click="clearCanvas" icon-pack="far" icon="fa-file"></vs-button>
                </div>
              </nv-tooltip>
            </template>
            <nv-tooltip :text="$t('DELETE')">
              <vs-button class="-mx-2 px-4" color="danger" type="filled" @click="deleteShape" icon-pack="far" icon="fa-trash-alt"></vs-button>
            </nv-tooltip>
            <template slot="append">
              <nv-tooltip :text="$t('DUPLICATE')">
                <div class="append-text btn-addon">
                  <vs-button color="danger" type="filled" @click="popupActive = true" icon-pack="far" icon="fa-clone"></vs-button>
                </div>
              </nv-tooltip>
            </template>
          </nv-input-group>
          <nv-input-group class="mr-2">
            <template slot="prepend">
              <nv-tooltip :text="$t('UNDO')">
                <div class="prepend-text btn-addon">
                  <vs-button color="primary" type="filled" @click="undo" :disabled="!canUndo" icon-pack="far" icon="fa-undo"></vs-button>
                </div>
              </nv-tooltip>
            </template>
            <template slot="append">
              <nv-tooltip :text="$t('REDO')">
                <div class="append-text btn-addon">
                  <vs-button color="primary" type="filled" @click="redo" :disabled="!canRedo" icon-pack="far" icon="fa-redo"></vs-button>
                </div>
              </nv-tooltip>
            </template>
          </nv-input-group>
          <nv-input-group class="mr-2">
            <template slot="prepend">
              <nv-tooltip :text="$t('ZOOM_IN')">
                <div class="prepend-text btn-addon">
                  <vs-button color="primary" type="filled" @click="zoomIn" icon-pack="far" icon="fa-search-plus"></vs-button>
                </div>
              </nv-tooltip>
            </template>
            <nv-tooltip :text="$t('RESET_ZOOM')">
              <vs-button class="-mx-2 px-4" color="primary" type="filled" @click="resetZoom">1:1</vs-button>
            </nv-tooltip>
            <template slot="append">
              <nv-tooltip :text="$t('ZOOM_OUT')">
                <div class="append-text btn-addon">
                  <vs-button color="primary" type="filled" @click="zoomOut" icon-pack="far" icon="fa-search-minus"></vs-button>
                </div>
              </nv-tooltip>
            </template>
          </nv-input-group>
          <nv-input-group class="mr-2">
            <template slot="prepend">
              <nv-tooltip :text="$t('TOGGLE_PALETTE')">
                <div class="prepend-text btn-addon">
                  <vs-button color="primary" type="filled" @click="togglePalette" icon-pack="far" icon="fa-th"></vs-button>
                </div>
              </nv-tooltip>
            </template>
            <template slot="append">
              <nv-tooltip :text="$t('TOGGLE_FULLSCREEN')">
                <div class="append-text btn-addon">
                  <vs-button color="primary" type="filled" @click="toggleFullScreen" icon-pack="far" :icon="toggleFullScreenIcon"></vs-button>
                </div>
              </nv-tooltip>
            </template>
          </nv-input-group>
          <nv-tooltip :text="$t('DOWNLOAD')">
            <vs-button class="mr-2" color="primary" type="filled" @click="exportFigurePNG" icon-pack="far" icon="fa-cloud-download"></vs-button>
          </nv-tooltip>
        </div>
        <div>
          <vs-button class="ml-2 px-3" color="primary" type="filled" @click="designWorkflow" icon-pack="far" icon="fa-save" :title="$t('SAVE')">{{ $t('SAVE') }}</vs-button>
        </div>
      </div>

      <div class="gutters-tiny mb-4" v-show="paletteVisible">
        <div v-for="(tool, index) in paletteTools" :key="index">
          <div :data-shape="tool.shapeName" class="float-left workflow-tool mr-2 draw2d_droppable">
            <div class="icon">
              <i :class="paletteToolStyle(tool)"></i>
            </div>
            <span class="font-w600">{{ tool.label }}</span>
          </div>
        </div>
        <div class="clearfix"></div>
      </div>

      <div class="workflow-title">{{ workflow.workflowName }}</div>
      <div class="designer-wrapper">
        <div class="canvas-wrapper">
          <div onselectstart="javascript:/*IE8 hack*/return false" id="canvas" style="width: 3000px; height: 3000px;"></div>
        </div>
        <transition name="slide-fade">
          <div class="props-wrapper bg-gray-lighter p-5" v-if="propsFormVisible">
            <component
              :is="propsComponentName"
              :key="propsTimestamp"
              :userData="userData"
              :workflow="workflow"
              @save="saveProps"
              @cancel="hidePropsForm">
            </component>
          </div>
        </transition>
      </div>
    </nv-card>
  </div>
</template>

<script>
import AlertMixin from '@/mixins/commons/AlertMixin'
import LoadingMixin from '@/mixins/commons/LoadingMixin'
import WorkflowPopup from '@/views/commons/workflow/WorkflowPopup'
import WorkflowService from '@/services/commons/WorkflowService'
import $ from 'jquery'
import 'jquery-ui/ui/widgets/droppable.js'
import draw2d from 'draw2d/dist/draw2d.js'
import ExitActivityProps from '@/views/commons/workflow-props/ExitActivityProps'
import ConditionActivityProps from '@/views/commons/workflow-props/ConditionActivityProps'
import UserActivityProps from '@/views/commons/workflow-props/UserActivityProps'
import AdjustmentActivityProps from '@/views/commons/workflow-props/AdjustmentActivityProps'
import ServiceActivityProps from '@/views/commons/workflow-props/ServiceActivityProps'
import AlertActivityProps from '@/views/commons/workflow-props/AlertActivityProps'

require('@/shapes/EnterActivity')
require('@/shapes/ExitActivity')
require('@/shapes/ConditionActivity')
require('@/shapes/UserActivity')
require('@/shapes/AdjustmentActivity')
require('@/shapes/ServiceActivity')
require('@/shapes/AlertActivity')

export default {
  name: 'workflow-designer',
  mixins: [AlertMixin, LoadingMixin],
  components: {
    WorkflowPopup,
    ExitActivityProps,
    ConditionActivityProps,
    UserActivityProps,
    AdjustmentActivityProps,
    ServiceActivityProps,
    AlertActivityProps
  },
  timers: {
    load: {
      time: 500,
      autostart: true,
      repeat: false
    },
    draftWorkflow: {
      time: process.env.VUE_APP_WF_DESIGNER_DRAFT_TIMER,
      autostart: process.env.VUE_APP_WF_DESIGNER_DRAFT_ENABLED === 'true',
      repeat: true
    }
  },
  data () {
    return {
      draftAlertActive: false,
      reduce: false,
      canvas: null,
      workflow: {},
      popupActive: false,
      shapeSelected: false,
      fullScreenMode: false,
      propsFormVisible: false,
      propsComponentName: null,
      propsTimestamp: Date.now(),
      selectedShape: null,
      userData: null,
      canUndo: false,
      canRedo: false,
      paletteVisible: true,
      paletteTools: [{
        shapeName: 'ExitActivity',
        iconName: 'far fa-stop-circle',
        label: this.$t('EXIT'),
        labelStyle: 'text-danger'
      }, {
        shapeName: 'ConditionActivity',
        iconName: 'far fa-question-circle',
        label: this.$t('CONDITION'),
        labelStyle: 'text-purple'
      }, {
        shapeName: 'UserActivity',
        iconName: 'far fa-user-circle',
        label: this.$t('USER'),
        labelStyle: 'text-dark'
      }, {
        shapeName: 'AdjustmentActivity',
        iconName: 'far fa-pencil-alt',
        label: this.$t('ADJUSTMENT'),
        labelStyle: 'text-grey'
      }, {
        shapeName: 'ServiceActivity',
        iconName: 'far fa-cog',
        label: this.$t('SERVICE'),
        labelStyle: 'text-success'
      }, {
        shapeName: 'AlertActivity',
        iconName: 'far fa-bell',
        label: this.$t('ALERT'),
        labelStyle: 'text-warning'
      }]
    }
  },
  computed: {
    toggleFullScreenIcon () {
      return this.fullScreenMode ? 'fa-window-restore' : 'fa-window-maximize'
    },
    isSidebarActive: {
      get () {
        return this.$store.state.isSidebarActive
      },
      set (val) {
        this.$store.commit('TOGGLE_IS_SIDEBAR_ACTIVE', val)
      }
    },
    reduceSidebar () {
      return Boolean(this.reduce && this.reduceButton)
    },
    reduceButton: {
      get () {
        return this.$store.state.reduceButton
      },
      set (val) {
        this.$store.commit('TOGGLE_REDUCE_BUTTON', val)
      }
    },
    sidebarItemsMin () {
      return this.$store.state.sidebarItemsMin
    }
  },
  methods: {
    async load () {
      // extend base canvas
      let self = this
      let WorkflowCanvas = draw2d.Canvas.extend({
        init: function (id) {
          this._super(id)
          this.setScrollArea('#' + id)
          this.currentDropConnection = null
        },
        onDrop: function (droppedDomNode, x, y, shiftKey, ctrlKey) {
          // eslint-disable-next-line
          let figure = eval('new ' + $(droppedDomNode).data('shape') + '({resizeable: false})')
          figure.on('dblclick', self.onFigureDoubleClicked)

          let command = new draw2d.command.CommandAdd(this, figure, x, y)
          this.getCommandStack().execute(command)
        }
      })

      // init canvas
      this.canvas = new WorkflowCanvas('canvas')
      this.canvas.installEditPolicy(new draw2d.policy.canvas.ShowGridEditPolicy())
      this.canvas.installEditPolicy(new draw2d.policy.connection.DragConnectionCreatePolicy({
        createConnection: function () {
          return new draw2d.Connection({
            stroke: 2,
            outlineStroke: 1,
            outlineColor: '#CCD1D9',
            color: '#AAB2BD',
            router: new draw2d.layout.connection.ManhattanConnectionRouter()
          })
        }
      }))
      this.canvas.on('select', this.onShapeSelected)

      // scroll fix
      this.canvas.fromDocumentToCanvasCoordinate = $.proxy(function (x, y) {
        return new draw2d.geo.Point(
          (x + window.pageXOffset - this.getAbsoluteX() + this.getScrollLeft()) * this.zoomFactor,
          (y + window.pageYOffset - this.getAbsoluteY() + this.getScrollTop()) * this.zoomFactor)
      }, this.canvas)
      this.canvas.fromCanvasToDocumentCoordinate = $.proxy(function (x, y) {
        return new draw2d.geo.Point(
          ((x * (1 / this.zoomFactor)) + this.getAbsoluteX() - this.getScrollLeft() - window.pageXOffset),
          ((y * (1 / this.zoomFactor)) + this.getAbsoluteY() - this.getScrollTop() - window.pageYOffset))
      }, this.canvas)

      // undo & redo listeners
      this.canvas.getCommandStack().addEventListener(function (event) {
        self.canUndo = event.getStack().canUndo()
        self.canRedo = event.getStack().canRedo()
      })

      // load figure data
      WorkflowService.workflows({
        workflowId: this.$route.params.id
      }).then(function (response) {
        self.workflow = response.data.workflowList[0] || {}
        self.draftAlertActive = self.workflow.draftData !== undefined && self.workflow.draftData !== ''
        if (self.workflow.modelData === undefined || self.workflow.modelData === '') {
          self.populateEmptyCanvas()
        } else {
          let reader = new draw2d.io.json.Reader()
          reader.unmarshal(self.canvas, self.workflow.modelData)
          self.canvas.figures.data.forEach(function (figure) {
            figure.on('dblclick', self.onFigureDoubleClicked)
            figure.setUserData(figure.getUserData())
          })
        }
        self.stopLoading('loading-container')
      })
    },
    zoomIn () {
      this.canvas.setZoom(this.canvas.getZoom() * 0.7, true)
    },
    resetZoom () {
      this.canvas.setZoom(1.0, true)
    },
    zoomOut () {
      this.canvas.setZoom(this.canvas.getZoom() * 1.3, true)
    },
    undo () {
      this.canvas.getCommandStack().undo()
      this.hidePropsForm()
    },
    redo () {
      this.canvas.getCommandStack().redo()
      this.hidePropsForm()
    },
    deleteShape () {
      let self = this
      if (this.canvas.getSelection().getSize() > 0) {
        this.hidePropsForm()
        this.canvas.getCommandStack().startTransaction()
        this.canvas.getSelection().each(function (i, node) {
          if (node && node.NAME !== 'EnterActivity') {
            let command = new draw2d.command.CommandDelete(node)
            self.canvas.getCommandStack().execute(command)
          }
        })
        this.canvas.getCommandStack().commitTransaction()
      }
    },
    duplicateWorkflow (record) {
      this.canvas.clear()
      if (record.modelData === undefined || record.modelData === '') {
        this.populateEmptyCanvas()
      } else {
        let self = this
        let reader = new draw2d.io.json.Reader()
        reader.unmarshal(this.canvas, record.modelData)
        self.canvas.figures.data.forEach(function (figure) {
          figure.on('dblclick', self.onFigureDoubleClicked)
          figure.setUserData(figure.getUserData())
        })
      }
    },
    clearCanvas () {
      this.canvas.clear()
      this.populateEmptyCanvas()
    },
    populateEmptyCanvas () {
      this.canvas.add(new window.EnterActivity({ x: 50, y: 150, resizeable: false }))
    },
    togglePalette () {
      this.paletteVisible = !this.paletteVisible
    },
    exportFigurePNG () {
      let writer = new draw2d.io.png.Writer()
      writer.marshal(this.canvas, function (png) {
        let downloadLink = document.createElement('a')
        downloadLink.href = png
        downloadLink.download = 'workflow-design.png'

        document.body.appendChild(downloadLink)
        downloadLink.click()
        document.body.removeChild(downloadLink)
      })
    },
    paletteToolStyle (tool) {
      return tool.iconName + ' ' + tool.labelStyle + ' fa-2x'
    },
    showShapeProps (shape) {
      this.selectedShape = shape
      this.userData = JSON.parse(JSON.stringify(shape.getUserData()))
      this.propsComponentName = shape.NAME + 'Props'
      this.propsTimestamp = Date.now()
      this.propsFormVisible = true
    },
    saveProps (userData) {
      this.hidePropsForm()
      this.selectedShape.setUserData(userData)
    },
    hidePropsForm () {
      this.propsFormVisible = false
    },
    onShapeSelected (emitter, event) {
      this.shapeSelected = event.figure !== null && event.figure.NAME !== 'EnterActivity'
    },
    onFigureDoubleClicked (emitter, event) {
      if (emitter.NAME && emitter.NAME !== 'EnterActivity') {
        this.showShapeProps(emitter)
      }
    },
    designWorkflow () {
      let modelData = ''
      let writer = new draw2d.io.json.Writer()
      writer.marshal(this.canvas, function (json) {
        modelData = JSON.stringify(json, null, 2)
      })

      let self = this
      this.startLoading('loading-container')
      WorkflowService.designWorkflow({
        workflowId: this.$route.params.id,
        modelData: modelData
      }).then(function (response) {
        if (response.status === 200) {
          self.stopLoading('loading-container')
          self.$router.back()
        }
      }).catch(function (error) {
        self.stopLoading('loading-container')
        self.showDangerAlert(error.response.data.errors)
      })
    },
    draftWorkflow () {
      let modelData = ''
      let writer = new draw2d.io.json.Writer()
      writer.marshal(this.canvas, function (json) {
        modelData = JSON.stringify(json, null, 2)
      })

      let self = this
      WorkflowService.draftWorkflow({
        workflowId: this.$route.params.id,
        modelData: modelData
      }).catch(function (error) {
        self.showDangerAlert(error.response.data.errors)
      })
    },
    toggleFullScreen () {
      if (this.fullScreenMode === false) {
        let docElm = document.documentElement
        this.launchIntoFullscreen(docElm)
      } else {
        this.exitFullscreen()
      }
    },
    launchIntoFullscreen (element) {
      if (element.requestFullscreen) {
        element.requestFullscreen()
      } else if (element.mozRequestFullScreen) {
        element.mozRequestFullScreen()
      } else if (element.webkitRequestFullscreen) {
        element.webkitRequestFullscreen()
      } else if (element.msRequestFullscreen) {
        element.msRequestFullscreen()
      }
      this.fullScreenMode = true
    },
    exitFullscreen () {
      if (document.exitFullscreen) {
        document.exitFullscreen()
      } else if (document.mozCancelFullScreen) {
        document.mozCancelFullScreen()
      } else if (document.webkitExitFullscreen) {
        document.webkitExitFullscreen()
      }
      this.fullScreenMode = false
    },
    checkFullScreenState () {
      let self = this
      let arr = ['fullscreenchange', 'webkitfullscreenchange', 'mozfullscreenchange', 'msfullscreenchange']
      arr.forEach(
        eventType => document.addEventListener(eventType, function () {
          let state = document.fullScreen || document.mozFullScreen || document.webkitIsFullScreen
          if (state) {
            self.fullScreenMode = true
            self.toggleReduce(true)
          } else {
            self.fullScreenMode = false
            self.toggleReduce(false)
          }
        }, false)
      )
    },
    toggleReduce (val) {
      this.reduceButton = val
      this.setSidebarWidth()
    },
    setSidebarWidth () {
      if (this.windowWidth < 1200) {
        if (this.windowWidth < 992) this.$store.commit('UPDATE_WINDOW_BREAKPOINT', 'md')
        else this.$store.commit('UPDATE_WINDOW_BREAKPOINT', 'lg')

        this.$store.commit('TOGGLE_IS_SIDEBAR_ACTIVE', false)
        if (this.reduceButton) this.reduce = false
        this.showCloseButton = true
        this.clickNotClose = false
        this.$store.dispatch('updateSidebarWidth', 'no-sidebar')
        this.$store.commit('UPDATE_SIDEBAR_ITEMS_MIN', false)
      } else {
        this.$store.commit('UPDATE_WINDOW_BREAKPOINT', 'xl')
        if (this.reduceButton) this.reduce = true
        else this.reduce = false

        this.$store.commit('TOGGLE_IS_SIDEBAR_ACTIVE', true)
        if (this.reduceButton && !this.isMouseEnter) this.$store.commit('UPDATE_SIDEBAR_ITEMS_MIN', true)
        else this.$store.commit('UPDATE_SIDEBAR_ITEMS_MIN', false)

        this.clickNotClose = true
        this.showCloseButton = false
        if (this.reduceSidebar) { this.$store.dispatch('updateSidebarWidth', 'reduced') } else { this.$store.dispatch('updateSidebarWidth', 'default') }
      }
    },
    loadDraft () {
      this.draftAlertActive = false
      this.canvas.clear()

      let self = this
      let reader = new draw2d.io.json.Reader()
      reader.unmarshal(self.canvas, self.workflow.draftData)
      self.canvas.figures.data.forEach(function (figure) {
        figure.on('dblclick', self.onFigureDoubleClicked)
        figure.setUserData(figure.getUserData())
      })
    },
    cancel () {
      let self = this
      WorkflowService.draftWorkflow({
        workflowId: this.$route.params.id,
        modelData: ''
      }).then(function (response) {
        if (response.status === 200) {
          self.$router.back()
        }
      }).catch(function (error) {
        self.showDangerAlert(error.response.data.errors)
      })
    }
  },
  watch: {
    reduce (val) {
      if (val === true) {
        this.$store.dispatch('updateSidebarWidth', 'reduced')
      } else {
        this.$store.dispatch('updateSidebarWidth', 'default')
      }

      setTimeout(function () {
        window.dispatchEvent(new Event('resize'))
      }, 100)
    },
    reduceButton () {
      this.setSidebarWidth()
    }
  },
  mounted: function () {
    this.$refs.main.focus()
    this.checkFullScreenState()
    this.startLoading('loading-container')
  },
  destroyed: function () {
    if (this.fullScreenMode) {
      this.exitFullscreen()
    }
  }
}
</script>
