import UomService from '@/services/commons/UomService'

let WorkflowTimeoutActivityMixin = {
  data () {
    return {
      timeIntervals: [],
      mutableUserData: {
        timeoutUomId: this.userData.timeoutUomId
      }
    }
  },
  methods: {
    async loadInternal () {
      let self = this
      UomService.uoms({
        uomTypeEnumId: 'UT_TIME_FREQ_MEASURE',
        orderByField: 'description',
        pageNoLimit: true
      }).then(function (response) {
        self.timeIntervals = response.data.uomList || []
      })
    }
  },
  mounted () {
    this.loadInternal()
  }
}

export default WorkflowTimeoutActivityMixin
