let WorkflowCrowdActivityMixin = {
  data () {
    return {
      crowdFormVisible: false,
      crowdTypes: [],
      crowdTypeEnumId: process.env.VUE_APP_WF_DESIGNER_DEFAULT_CROWD_TYPE,
      userId: '',
      userFullName: '',
      userGroupId: '',
      userGroupDescription: ''
    }
  },
  methods: {
    crowdName (crowd) {
      if (crowd.crowdTypeEnumId === 'WF_CROWD_USER') {
        return crowd.userFullName
      } else if (crowd.crowdTypeEnumId === 'WF_CROWD_USER_GROUP') {
        return crowd.userGroupDescription
      } else {
        return this.$t('INITIATOR')
      }
    },
    showCrowdForm () {
      this.crowdFormVisible = true
    },
    hideCrowdForm () {
      this.crowdFormVisible = false
    },
    selectUserRecord (record) {
      this.userId = record.userId
      this.userFullName = record.userFullName
    },
    selectUserGroupRecord (record) {
      this.userGroupId = record.userGroupId
      this.userGroupDescription = record.description
    }
  }
}

export default WorkflowCrowdActivityMixin
