<template>
  <div>
    <div class="nv-row mb-4">
      <div class="nv-col w-full">
        <label class="vs-input--label">{{ $t('ACTIVITY_DESCRIPTION') }}</label>
        <vs-input class="w-full" v-model="mutableUserData.activityDescription"/>
      </div>
    </div>

    <div class="nv-row mb-4">
      <div class="nv-col w-full">
        <label class="vs-input--label">{{ $t('CONDITION_TYPE') }}</label>
        <ul class="flex justify-start mt-2">
          <li class="mr-4" v-for="(enumeration, index) in conditionTypes" :key="index" :value="enumeration.enumId">
            <vs-radio v-model="mutableUserData.conditionTypeEnumId" :vs-value="enumeration.enumId">{{ enumeration.description }}</vs-radio>
          </li>
        </ul>
      </div>
    </div>

    <div class="nv-row mb-4">
      <div class="nv-col w-full" v-if="mutableUserData.conditionTypeEnumId === 'WF_CONDITION_FIELD'">
        <label class="vs-input--label">{{ $t('CONDITIONS') }}</label>
        <vs-table :data="mutableUserData.conditions" class="table-no-header">
          <template slot="thead">
            <vs-th sort-key="logicalGroup">
              {{ $t('GROUP') }}
            </vs-th>
            <vs-th>
              {{ $t('FIELD') }}
