            <vs-th>
              {{ $t('OPERATOR') }}
            </vs-th>
            <vs-th>
              {{ $t('VALUE') }}
            </vs-th>
            <vs-th>
              {{ $t('OPTIONS') }}
            </vs-th>
          </template>

          <template slot-scope="{data}">
            <vs-tr :key="index" v-for="(tr, index) in data" :data="tr">
              <vs-td :data="data[index]" class="text-center">
                <vs-dropdown vs-trigger-click>
                  <vs-button class="btn-drop" :color="groupColor(data[index])" type="border" icon-pack="fas" icon="fa-circle"></vs-button>
                  <vs-dropdown-menu class="dropdown-small">
                    <vs-dropdown-item v-for="(color, colorIndex) in groupColors" :key="colorIndex" @click="data[index].logicalGroup = colorIndex">
                      <vs-icon :color="groupColors[colorIndex]" icon-pack="fas" icon="fa-circle"></vs-icon>
                    </vs-dropdown-item>
                  </vs-dropdown-menu>
                </vs-dropdown>
              </vs-td>
              <vs-td :data="data[index]">
                <vs-select autocomplete class="w-full" :placeholder="$t('SELECT_VALUE')" v-model="data[index].fieldName">
                  <vs-select-item v-for="(field, index) in conditionFields" :key="index" :value="field.fieldName" :text="field.displayName"/>
                </vs-select>
              </vs-td>
              <vs-td :data="data[index]">
                <template v-if="fieldByName(data[index].fieldName).fieldTypeEnumId === 'ENTITY_FLD_TEXT'">
                  <vs-select autocomplete class="w-full" :placeholder="$t('SELECT_VALUE')" v-model="data[index].operator">
                    <vs-select-item v-for="(operator, index) in textOperators" :key="index" :value="operator.description" :text="operator.description"/>
                  </vs-select>
