                <template v-if="fieldByName(data[index].fieldName).fieldTypeEnumId === 'ENTITY_FLD_BOOLEAN'">
                  <vs-select autocomplete class="w-full" :placeholder="$t('SELECT_VALUE')" v-model="data[index].operator">
                    <vs-select-item v-for="(operator, index) in booleanOperators" :key="index" :value="operator.enumId" :text="operator.description"/>
                  </vs-select>
                </template>
              </vs-td>
              <vs-td :data="data[index]">
                <template v-if="fieldByName(data[index].fieldName).fieldTypeEnumId === 'ENTITY_FLD_TEXT'">
                  <vs-input v-model="data[index].value" type="text" :disabled="data[index].operator === 'TXT_EMPTY' || data[index].operator === 'TXT_NOT_EMPTY'"/>
                </template>
                <template v-if="fieldByName(data[index].fieldName).fieldTypeEnumId === 'ENTITY_FLD_NUMBER'">
                  <vs-input v-model="data[index].value" type="number"/>
                </template>
                <template v-if="fieldByName(data[index].fieldName).fieldTypeEnumId === 'ENTITY_FLD_DATE'">
