                </template>
                <template v-if="fieldByName(data[index].fieldName).fieldTypeEnumId === 'ENTITY_FLD_BOOLEAN'">
                  <vs-input v-model="data[index].value" type="text" :disabled="true"/>
                </template>
              </vs-td>
              <vs-td class="text-center" :data="data[index]">
                <nv-input-group>
                  <template slot="prepend">
                    <div class="prepend-text btn-addon">
                      <vs-button @click="deleteCondition(index)" :disabled="index === 0">
                        <i class="fa fa-times"></i>
                      </vs-button>
                    </div>
                  </template>
                  <template slot="append">
                    <div class="append-text btn-addon">
                      <vs-button @click="addCondition">
                        <i class="fa fa-plus"></i>
                      </vs-button>
                    </div>
                  </template>
                </nv-input-group>
              </vs-td>
            </vs-tr>
          </template>
        </vs-table>
      </div>

      <div class="nv-col w-full" v-if="mutableUserData.conditionTypeEnumId === 'WF_CONDITION_VARIABLE'">
        <label class="vs-input--label">{{ $t('CONDITIONS') }}</label>
        <vs-table :data="mutableUserData.conditions" class="table-no-header">
          <template slot="thead">
            <vs-th sort-key="logicalGroup">
              {{ $t('GROUP') }}
            </vs-th>
            <vs-th>
              {{ $t('VARIABLE') }}
            </vs-th>
            <vs-th>
              {{ $t('OPERATOR') }}
