              </vs-td>
              <vs-td :data="data[index]">
                <vs-select autocomplete class="w-full" :placeholder="$t('SELECT_VALUE')" v-model="data[index].variableName">
                  <vs-select-item v-for="(variable, index) in variables" :key="index" :value="variable.variableName" :text="variable.variableName"/>
