                  <vs-select autocomplete class="w-full" :placeholder="$t('SELECT_VALUE')" v-model="data[index].operator">
                    <vs-select-item v-for="(operator, index) in textOperators" :key="index" :value="operator.enumId" :text="operator.description"/>
                  </vs-select>
                </template>
                <template v-if="variableByName(data[index].variableName).variableTypeEnumId === 'WF_VAR_NUMBER'">
                  <vs-select autocomplete class="w-full" :placeholder="$t('SELECT_VALUE')" v-model="data[index].operator">
