                  </vs-select>
                </template>
              </vs-td>
              <vs-td :data="data[index]">
                <template v-if="variableByName(data[index].variableName).variableTypeEnumId === 'WF_VAR_TEXT'">
                  <vs-input v-model="data[index].value" type="text" :disabled="data[index].operator === 'TXT_EMPTY' || data[index].operator === 'TXT_NOT_EMPTY'"/>
                </template>
                <template v-if="variableByName(data[index].variableName).variableTypeEnumId === 'WF_VAR_NUMBER'">
                  <vs-input v-model="data[index].value" type="number"/>
                </template>
              </vs-td>
              <vs-td class="text-center" :data="data[index]">
                <nv-input-group>
                  <template slot="prepend">
                    <div class="prepend-text btn-addon">
                      <vs-button @click="deleteCondition(index)" :disabled="index === 0">
                        <i class="fa fa-times"></i>
                      </vs-button>
                    </div>
                  </template>
                  <template slot="append">
                    <div class="append-text btn-addon">
                      <vs-button @click="addCondition">
                        <i class="fa fa-plus"></i>
                      </vs-button>
                    </div>
                  </template>
                </nv-input-group>
              </vs-td>
            </vs-tr>
          </template>
        </vs-table>
      </div>

      <div class="nv-col w-full" v-if="mutableUserData.conditionTypeEnumId === 'WF_CONDITION_APPROVAL'">
        <div class="flex justify-left mb-2">
          <div class="vs-input--label mr-2">{{ $t('CONDITIONS') }}</div>
          <div>
            <vs-button class="m-0 p-0" color="primary" type="flat" @click="showCrowdForm">
              <i class="far fa-plus-circle text-primary"></i>
            </vs-button>
          </div>
        </div>

        <div class="inline-form p-4 mt-4 mb-4" v-if="crowdFormVisible">
          <div class="nv-row mb-4">
            <div class="nv-col sm:w-1/3 w-full">
              <vs-select autocomplete class="w-full" v-model="crowdTypeEnumId">
                <vs-select-item v-for="(enumeration, index) in crowdTypes" :key="index" :value="enumeration.enumId" :text="enumeration.description"/>
              </vs-select>
            </div>
            <div class="nv-col sm:w-2/3 w-full pl-0">
              <user-lookup :title="$t('USER')" valueField="userId" displayField="userFullName" @select-record="selectUserRecord" v-show="crowdTypeEnumId === 'WF_CROWD_USER'"/>
              <user-group-lookup :title="$t('USER_GROUP')" valueField="userGroupId" displayField="description" @select-record="selectUserGroupRecord" v-show="crowdTypeEnumId === 'WF_CROWD_USER_GROUP'"/>
              <vs-input class="w-full" :disabled="true" v-show="crowdTypeEnumId === 'WF_CROWD_INITIATOR'"/>
            </div>
          </div>

          <div class="nv-row mb-4" v-if="crowdTypeEnumId === 'WF_CROWD_USER_GROUP'">
            <div class="nv-col sm:w-1/2 w-full required">
              <label class="vs-input--label">{{ $t('MIN_APPROVALS') }}</label>
              <vs-input class="w-full" v-model="minApprovals" type="number"/>
            </div>
            <div class="nv-col sm:w-1/2 w-full required">
              <label class="vs-input--label">{{ $t('MIN_REJECTIONS') }}</label>
              <vs-input class="w-full" v-model="minRejections" type="number"/>
            </div>
          </div>

          <div class="nv-row">
            <div class="nv-col w-full">
              <vs-button class="px-5 py-3 mr-4 border-solid" @click="createCrowd" color="primary" type="filled">{{ $t('SUBMIT') }}</vs-button>
              <vs-button class="p-3 mr-4" @click="hideCrowdForm" color="primary" type="border">{{ $t('CANCEL') }}</vs-button>
            </div>
          </div>
        </div>

        <div class="alert-container" v-if="mutableUserData.crowds.length === 0">
          <vs-alert :active.sync="mutableUserData.crowds.length === 0" color="warning">
            <i class="far fa-exclamation-circle pr-1"></i> {{ $t('NO_DATA_FOUND') }}
          </vs-alert>
        </div>
        <vs-table :data="mutableUserData.crowds" class="mb-4 table-no-header" v-else>
          <template slot="thead">
            <vs-th sort-key="logicalGroup">
              {{ $t('GROUP') }}
            </vs-th>
            <vs-th>
              {{ $t('CROWD') }}
            </vs-th>
            <vs-th>
              {{ $t('MIN_APPROVALS') }}
            </vs-th>
            <vs-th>
              {{ $t('OPTIONS') }}
            </vs-th>
          </template>
          <template slot-scope="{data}">
            <vs-tr :key="index" v-for="(tr, index) in data" :data="tr">
              <vs-td :data="data[index]" class="text-center">
                <vs-dropdown vs-trigger-click>
                  <vs-button class="btn-drop" :color="groupColor(data[index])" type="border" icon-pack="fas" icon="fa-circle"></vs-button>
                  <vs-dropdown-menu class="dropdown-small">
                    <vs-dropdown-item v-for="(color, colorIndex) in groupColors" :key="colorIndex" @click="data[index].logicalGroup = colorIndex">
                      <vs-icon :color="groupColors[colorIndex]" icon-pack="fas" icon="fa-circle"></vs-icon>
                    </vs-dropdown-item>
                  </vs-dropdown-menu>
                </vs-dropdown>
              </vs-td>
              <vs-td :data="data[index]">
                {{ crowdName(data[index]) }}
              </vs-td>
              <vs-td :data="data[index]">
                <span class="mr-2"><i class="far fa-check-circle text-success"></i> {{ data[index].minApprovals }}</span>
                <span class="mr-4"><i class="far fa-times-circle text-danger"></i> {{ data[index].minRejections }}</span>
              </vs-td>
              <vs-td :data="data[index]">
                <vs-button class="p-3 mr-4" color="danger" @click="deleteCrowd(index)" icon-pack="far" icon="fa-trash-alt">{{ $t('DELETE') }}</vs-button>
              </vs-td>
            </vs-tr>
          </template>
        </vs-table>
      </div>

      <div class="nv-col w-full" v-if="mutableUserData.conditionTypeEnumId === 'WF_CONDITION_SCRIPT'">
        <label class="vs-input--label">{{ $t('CONDITIONS') }}</label>
        <vs-table :data="mutableUserData.conditions" class="table-no-header">
          <template slot="thead">
            <vs-th sort-key="logicalGroup">
              {{ $t('GROUP') }}
            </vs-th>
            <vs-th>
              {{ $t('SCRIPT') }}
            </vs-th>
            <vs-th>
              {{ $t('OPTIONS') }}
            </vs-th>
          </template>

          <template slot-scope="{data}">
            <vs-tr v-for="(tr, index) in data" :data="tr" :key="index">
              <vs-td :data="data[index]" class="text-center">
                <vs-dropdown vs-trigger-click>
                  <vs-button class="btn-drop" :color="groupColor(data[index])" type="border" icon-pack="fas" icon="fa-circle"></vs-button>
                  <vs-dropdown-menu class="dropdown-small">
                    <vs-dropdown-item v-for="(color, colorIndex) in groupColors" :key="colorIndex" @click="data[index].logicalGroup = colorIndex">
                      <vs-icon :color="groupColors[colorIndex]" icon-pack="fas" icon="fa-circle"></vs-icon>
                    </vs-dropdown-item>
                  </vs-dropdown-menu>
                </vs-dropdown>
              </vs-td>
              <vs-td :data="data[index]">
                <div class="code-editor">
                  <codemirror v-model="data[index].script" :options="javascriptOptions" class="workflow-code-editor"></codemirror>
                </div>
              </vs-td>
              <vs-td class="text-center" :data="data[index]">
                <nv-input-group>
                  <template slot="prepend">
                    <div class="prepend-text btn-addon">
                      <vs-button @click="deleteCondition(index)" :disabled="index === 0">
                        <i class="fa fa-times"></i>
                      </vs-button>
                    </div>
                  </template>
                  <template slot="append">
                    <div class="append-text btn-addon">
                      <vs-button @click="addCondition">
                        <i class="fa fa-plus"></i>
                      </vs-button>
                    </div>
                  </template>
                </nv-input-group>
              </vs-td>
            </vs-tr>
          </template>
        </vs-table>
      </div>
    </div>

    <div class="nv-row mb-4">
      <div class="nv-col w-full">
        <vs-button class="px-5 py-3 mr-4 border-solid" @click="save" color="primary" type="filled">{{ $t('SAVE') }}</vs-button>
        <vs-button class="p-3 mr-4" @click="cancel" color="primary" type="border" t>{{ $t('CANCEL') }}</vs-button>
      </div>
    </div>
  </div>
</template>

<script>
import UserLookup from '@/views/nucleus/user/UserLookup'
import UserGroupLookup from '@/views/nucleus/user-group/UserGroupLookup'
import CodeEditorMixin from '@/mixins/commons/CodeEditorMixin'
import WorkflowActivityMixin from '@/mixins/commons/WorkflowActivityMixin'
import WorkflowCrowdActivityMixin from '@/mixins/commons/WorkflowCrowdActivityMixin'
import WorkflowGroupActivityMixin from '@/mixins/commons/WorkflowGroupActivityMixin'
import EntityService from '@/services/commons/EntityService'
import EnumerationService from '@/services/commons/EnumerationService'
import WorkflowService from '@/services/commons/WorkflowService'

export default {
  name: 'condition-activity-props',
  mixins: [CodeEditorMixin, WorkflowActivityMixin, WorkflowCrowdActivityMixin, WorkflowGroupActivityMixin],
  components: {
    UserLookup,
    UserGroupLookup
  },
  data () {
    return {
      conditionTypes: [],
      conditionFields: [],
      variables: [],
      textOperators: [],
      numberOperators: [],
      dateOperators: [],
      booleanOperators: [],
      minApprovals: 1,
      minRejections: 1
    }
  },
  methods: {
    async load () {
      let self = this
      EnumerationService.enumerations({
        enumTypeId: 'WorkflowConditionType',
        orderByField: 'sequenceNum,description'
      }).then(function (response) {
        self.conditionTypes = response.data.enumerationList || []
      })

      EnumerationService.enumerations({
        enumTypeId: 'WorkflowCrowdType',
        orderByField: 'sequenceNum,description'
      }).then(function (response) {
        self.crowdTypes = response.data.enumerationList || []
      })

      WorkflowService.workflowVariables({
        workflowId: this.workflow.workflowId,
        orderByField: 'variableName'
      }).then(function (response) {
        self.variables = response.data.workflowVariableList || []
      })

      await EnumerationService.enumerations({
        enumTypeId: 'TextComparisonOperator',
        orderByField: 'sequenceNum,description'
      }).then(function (response) {
        self.textOperators = response.data.enumerationList || []
      })

      await EnumerationService.enumerations({
        enumTypeId: 'NumberComparisonOperator',
        orderByField: 'sequenceNum,description'
      }).then(function (response) {
        self.numberOperators = response.data.enumerationList || []
      })

      await EnumerationService.enumerations({
        enumTypeId: 'DateComparisonOperator',
        orderByField: 'sequenceNum,description'
      }).then(function (response) {
        self.dateOperators = response.data.enumerationList || []
      })

      await EnumerationService.enumerations({
        enumTypeId: 'BooleanComparisonOperator',
        orderByField: 'sequenceNum,description'
      }).then(function (response) {
        self.booleanOperators = response.data.enumerationList || []
      })

      EntityService.entityFields({
        entityName: this.workflow.primaryViewEntityName,
        orderByField: 'fieldName'
      }).then(function (response) {
        self.conditionFields = response.data.entityFieldList || []
        if (self.mutableUserData.conditions.length === 0) {
          self.addCondition()
        }
      })
    },
    fieldByName (fieldName) {
      for (let i = 0; i < this.conditionFields.length; i++) {
        if (this.conditionFields[i].fieldName === fieldName) {
          return this.conditionFields[i]
        }
      }
      return {}
    },
    variableByName (variableName) {
      for (let i = 0; i < this.variables.length; i++) {
        if (this.variables[i].variableName === variableName) {
          return this.variables[i]
        }
      }
      return {}
    },
    addCondition () {
      if (this.mutableUserData.conditionTypeEnumId === 'WF_CONDITION_FIELD') {
        let field = this.conditionFields[0]
        if (field) {
          let fieldName = field.fieldName
          let fieldTypeEnumId = field.fieldTypeEnumId
          let operator = fieldTypeEnumId === 'ENTITY_FLD_DATE' ? this.dateOperators[0].value : (fieldTypeEnumId === 'ENTITY_FLD_NUMBER' ? this.numberOperators[0].value : this.textOperators[0].value)
          this.mutableUserData.conditions.push({
            logicalGroup: this.logicalGroup,
            fieldName: fieldName,
            operator: operator,
            value: ''
          })
        }
      } else if (this.mutableUserData.conditionTypeEnumId === 'WF_CONDITION_VARIABLE') {
        let variable = this.variables[0]
        if (variable) {
          let variableName = variable.variableName
          let variableTypeEnumId = variable.variableTypeEnumId
          let operator = variableTypeEnumId === 'WF_VAR_TEXT' ? this.numberOperators[0].value : this.textOperators[0].value
          this.mutableUserData.conditions.push({
            logicalGroup: this.logicalGroup,
            variableName: variableName,
            operator: operator,
            value: ''
          })
        }
      } else if (this.mutableUserData.conditionTypeEnumId === 'WF_CONDITION_SCRIPT') {
        this.mutableUserData.conditions.push({
          logicalGroup: this.logicalGroup,
          script: ''
        })
      }
    },
    deleteCondition (index) {
      this.mutableUserData.conditions.splice(index, 1)
    },
    resetConditions () {
      this.mutableUserData.conditions = []
      this.addCondition()
    },
    createCrowd () {
      if (this.crowdTypeEnumId === 'WF_CROWD_INITIATOR') {
        this.mutableUserData.crowds.push({
          logicalGroup: this.logicalGroup,
          crowdTypeEnumId: this.crowdTypeEnumId,
          minApprovals: 1,
          minRejections: 1
        })
      } else if (this.crowdTypeEnumId === 'WF_CROWD_USER' && this.userId) {
        this.mutableUserData.crowds.push({
          logicalGroup: this.logicalGroup,
          crowdTypeEnumId: this.crowdTypeEnumId,
          userId: this.userId,
          userFullName: this.userFullName,
          minApprovals: 1,
          minRejections: 1
        })
      } else if (this.crowdTypeEnumId === 'WF_CROWD_USER_GROUP' && this.userGroupId) {
        this.mutableUserData.crowds.push({
          logicalGroup: this.logicalGroup,
          crowdTypeEnumId: this.crowdTypeEnumId,
          userGroupId: this.userGroupId,
          userGroupDescription: this.userGroupDescription,
          minApprovals: this.minApprovals,
          minRejections: this.minRejections
        })
      }
      this.hideCrowdForm()
    },
    deleteCrowd (index) {
      this.mutableUserData.crowds.splice(index, 1)
    }
  },
  mounted () {
    this.load()
    this.mutableUserData.activityDescription = this.mutableUserData.activityDescription ? this.mutableUserData.activityDescription : ''
  },
  watch: {
    'mutableUserData.conditionTypeEnumId': function (val) {
      this.resetConditions()
    }
  }
}
</script>
